@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
/* #1f9751 Green org*/

.Home_container__2lDpo {
  display: grid;
  grid-template-columns: 2fr 1fr;
  max-width: 960px;
  margin: 60px auto;
}
.Home_content__1dO_- {
  padding-right: 30px;
}
.Home_sidebar__TLpaU {
  padding-left: 30px;
}
.Home_sidebar__TLpaU form {
  padding: 20px;
  background: #7745a5;
  border-radius: 10px;
}
.Home_sidebar__TLpaU input,
.Home_sidebar__TLpaU select {
  display: block;
  width: 100%;
  padding: 10px;
  margin-top: 8px;
  box-sizing: border-box;
  border: 0;
  border-radius: 4px;
  color: #555;
  font-size: 1em;
}
.Home_sidebar__TLpaU label {
  margin: 0 auto 20px;
  display: block;
  color: #fff;
}
.Home_sidebar__TLpaU button {
  color: #fff;
  border: 2px solid #fff;
  padding: 6px 12px;
  background-color: transparent;
  font-size: 1em;
  border-radius: 4px;
  cursor: pointer;
  display: block;
  width: 100%;
}
.Home_sidebar__TLpaU h3 {
  color: #fff;
  margin-bottom: 20px;
}
.Home_sidebar__TLpaU aside {
  margin-top: 40px;
  color: #555;
}
.Home_sidebar__TLpaU aside li {
  margin: 10px;
}

.Home_sidebar__TLpaU button:hover {
  color: #7745a5;
  background-color: white;
}

/* transactions */
.Home_transactions__2-UhT li {
  margin: 40px auto;
  border: 1px solid #f2f2f2;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
  padding: 15px;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  border-left: 4px solid #7745a5;
}
.Home_transactions__2-UhT .Home_name__1kjvE {
  color: #f2f2f2;
  font-size: 1.4em;
}
.Home_transactions__2-UhT .Home_amount__2j6xl {
  margin-left: auto;
  margin-right: 40px;
  color: #f2f2f2;
  font-weight: bold;
  font-size: 1.6em;
}
.Home_transactions__2-UhT button {
  position: absolute;
  top: 0;
  right: 0;
  background: #ddd;
  color: #777;
  border: none;
  padding: 12px 8px;
  text-align: center;
  line-height: 0;
  font-size: 0.9em;
  cursor: pointer;
}

