/* navbar styles */
.Navbar_navbar__3gkqs {
  color: #fff;
  width: 100%;
  background: #525668;
  padding: 20px 10px;
  box-sizing: border-box;
}
.Navbar_navbar__3gkqs ul {
  display: flex;
  margin: 0 auto;
  max-width: 960px;
  align-items: center;
}
.Navbar_title__1pNMA {
  margin-right: auto;
  font-weight: bold;
  letter-spacing: 1px;
  font-size: 1.2em;
}
.Navbar_navbar__3gkqs button, .Navbar_navbar__3gkqs a {
  margin-left: 16px;
}
.Navbar_navbar__3gkqs a {
  color: rgb(240, 240, 240);
  text-decoration: none;
}
.Navbar_navbar__3gkqs a:hover {
  color: rgb(77, 219, 89);
  text-decoration: none;
}

/*  Base Home  */

.base-home {
  background-color: rgb(240, 241, 241);
  max-width: 800px;
  margin: 120px auto;
  padding: 20px;
  border-radius: 20px;
}
.base-home .base-brand {
  font-size: 3em;
  color: rgb(54, 53, 53);
}
.base-home .base-welcome {
  padding-top: 30px;
  padding-left: 20px;
  font-size: 2.2em;
  color: rgb(54, 53, 53);
}
.base-home .base-head {
  padding-left: 20px;
  font-size: 1.7em;
  color: rgb(54, 53, 53);
}
.base-home .base-body {
  padding: 20px;
  padding-bottom: 30px;
  font-size: 1em;
  color: rgb(54, 53, 53);
}

.base-home .link {
  color: rgb(54, 53, 53);
  text-decoration: none;
}
.base-home .link:hover {
  color: rgb(77, 219, 89);
  text-decoration: none;
}

/* base styles */
body {
  font-family: Poppins, sans-serif;
  margin: 0;
  font-size: 1.1em;
  background-color: rgb(54, 53, 53);
}
ul, li, p, h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
  color: rgb(230, 230, 230);
}
ul {
  list-style-type: none;
}
.btn {
  background: none;
  border: 2px solid #1f9751;
  padding: 6px 12px;
  border-radius: 4px;
  color: #1f9751;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
.btn:hover {
  background: #1f9751;
  color: #fff;
}
